.main-nav {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin: 1.5vh auto;

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 60px;
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: $mobile-width) {
    font-size: 1em;
    line-height: 1em;
  }

  @media (min-width: $desktop-width) {
    font-size: 1em;
    line-height: 1em;
  }
}

.main-nav__link {
  display: block;
  font-size: 1.5em;
  line-height: 1.5em;
  color: $black;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  height: 40px;
  width: 80%;

  @media (min-width: $desktop-width) {
    height: 25px;
    font-size: 1em;
    line-height: 1em;
    text-align: left;
    width: 100%;
  }

  &--active {
    color: transparentize($color: $pink, $amount: 0.7);
  }
}

.main-nav__link:hover {
  color: $pink;
}

.main-nav__link:focus {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
}

.main-nav__link:active {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
  color: transparentize($color: $pink, $amount: 0.7);
}

.main-nav-closed .main-nav__item {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.main-nav__item:first-child .main-nav__item {
  display: block;
}

.main-nav__item:first-child .main-nav__link {
  font-weight: bold;
}

.main-nav__item:last-child {
  @media (min-width: $desktop-width) {
    width: 75%;
  }
}

.main-nav-opened .main-nav__list{
  display: block;
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  min-height: 70%;
  width: calc(100% - 20vw);
  font-size: 1.2em;
  line-height: 1.2em;
  padding-left: 5vw;
  padding-top: 10vh;
  background-color: $bg-mobile;
  transition: all 300ms ease-in-out;

  @media (min-width: $desktop-width) {
    background-color: $bg;
  }
}

.main-nav__toggle {
  position: relative;
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-right: 2vw;
   
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-nav__toggle input {
  display: flex;
  width: 100%;
  height: 60px;
  position: absolute;
  cursor: pointer;
  opacity: 0; 
  z-index: 10; 
}

.main-nav__toggle span{
  width: 50px;
  height: 6px;
  margin-bottom: 10px;
  z-index: 11;
  background: $black;
  border-radius: 4px;
  -webkit-transition: all .5s cubic-bezier(.08,.81,.87,.71);
  -moz-transition: all .5s cubic-bezier(.08,.81,.87,.71);
  -ms-transition: all .5s cubic-bezier(.08,.81,.87,.71);
  -o-transition: all .5s cubic-bezier(.08,.81,.87,.71);
  transition: all .5s cubic-bezier(.08,.81,.87,.71);
}

#span1 {
  transform-origin: 4px 0px;
}

#span3 {
  transform-origin: bottom right;
}

.main-nav__toggle input:checked ~ #span1 {
  background-color: $black;
  transform: rotate(45deg) translate(8px);
}
.main-nav__toggle input:checked ~ #span2 {
  background-color: $black;
  transform: rotate(495deg) translate(4px);
}
.main-nav__toggle input:checked ~ #span3 {
  background-color: $black;
  transform: rotate(45deg);
  opacity: 0;
}

.shadow {
  box-shadow: 0 0 0 9999px rgba(0,0,0, 0.3);
}

.middle .main-nav__link {
  text-decoration: none;
  font-size: 1em;
  position: relative;
  transition: all 0.6s;

  &:before {
      content: "";
      width: 0;
      height: 0.1em;
      position: absolute;
      bottom: 0;
      left: 50%;
      background: $black;
      transition: all 0.3s;
  }
  &:hover {
      &:before {
          width: 100%;
          left: 0;
          background: $pink;
      }
  }
}