.social {
  font-size: 1em;
  line-height: 1em;
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  min-height: 50px;
  width: 8em;

  @media (min-width: $desktop-width) {
    flex-direction: column;
    justify-content: space-between;
    position:  fixed;
    width: 7em;
    bottom: 2vh;
    left: 2vw;
    min-height: 120px;
  }
}

.social__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  color: $black;

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
  }
}

.social__link:hover {
  color: $pink;
}

.social__link:active {
  color: transparentize($color: $pink, $amount: 0.7);
}

.social__item {
  height: 25px;
  width: 100%;
}

.social__content {
  margin: 0;

  @media (min-width: $mobile-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: block;
  }
}

.social__svg {
  width: 22px;
  height: 22px;

  @media (min-width: $desktop-width) {
    margin-left: 1vw;
  }
}

.social__svg:hover {
  fill: $pink;
  stroke: $pink;
}