.skills {
   display: flex; 
   flex-direction: column;
   justify-content: space-between;
   align-items: center;

   @media (min-width: $desktop-width) {
     max-width: 840px;  
     width: 70%;
     margin: 0 auto;
   }
}

.skills__wrapper {
  width: 100%;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
}

.skills__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 6%; 
    @media (min-width: $desktop-width) {
       margin-left: 160px;
    }
}

.skills__list {
  display: flex; 
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  list-style: none;
}

.skills__heading {
  font-size: 2em;
  margin: 0;  
}