.pink {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  
  @media (min-width: $desktop-width) {
    width: 65%;  
    max-width: 800px;
    margin: 5vh auto;
    min-height: 1050px;
  }
}

.pink__link {
  font-size: 2em;
  line-height: 1.5em;
  text-decoration: none;  
  color: $black;
  cursor: pointer;
  align-self: center;
  
  @media (min-width: $desktop-width) {
    align-self: flex-start;
    font-size: 2.5em;
    line-height: 1em;  
    margin-top: 1em;
  }
}

.pink__link:hover {
  color: $pink;
}

.pink__link:active {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
  color: transparentize($color: $pink, $amount: 0.7);
}

.pink__link:focus {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
}

.pink__content {
  margin: 0;
  text-align: justify;
  text-indent: 1em;
  font-size: 1em;
  line-height: 1.4em;
  width: 80%;
  max-width: 330px;

  @media (min-width: $desktop-width) {
    order: 4;
    margin-right: 8vw;
  }
}

.pink__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  min-height: 460px;
  width: 100%;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 700px;
  }
} 

.pink__image {

  @media (min-width: $desktop-width) {
    display: block;
    position: relative;
    cursor: pointer;
  }
}

.pink__image:first-child {
  z-index: 1;
  top: 0;
}

.pink__image:last-child {
  left: 0;
  z-index: 3;
}

.pink__pic {
  display: block;

  @media (min-width: $desktop-width) {
    position: relative;
    transition: 1s;
  }  
}

.pink__pic:hover {
  transform: scale(1.1);
}

.pink__pic--one {
  @media (min-width: $desktop-width) {
    z-index: 1;
    width: 550px;
    height: 390px;
    margin-top: 3vh;
  }
}

.pink__image:nth-child(2n+2) {
  position: absolute;
  z-index: 2;
  top: 15.5%;
  right: 60%;
}

.pink__pic--three {

  @media (min-width: $desktop-width) {
    z-index: 3;
    top: auto;
  }
}

.pink__pic--two,
.pink__pic--three {

  @media (min-width: $mobile-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: block;
  }
}