.sedona {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 65%;  
    max-width: 800px;
    margin: 0 auto;
  }
}

.sedona__wrap {
  text-align: center;  

  @media (min-width: $desktop-width) {
    align-self: flex-start;
    text-align: justify;
    min-height: 140px;
  }
}

.sedona__text {
  @media (min-width: $desktop-width)  {
    display: flex;
    justify-content: space-between; 
    margin-top: 17vh;
    width: 100%;
    min-height: 500px;
  }
}

.sedona__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  min-height: 600px;  
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 100%;  
    min-height: 700px;
    justify-content: space-between;
    flex-direction: row;  
    flex-wrap: wrap;
  }
}

.sedona__image {
  cursor: pointer;
}

.sedona__pic--three {
  @media (min-width: $desktop-width) {
    margin-right: 20vw;  
    margin-top: 4vh;
  }
}

.sedona__pic {
  display: block;  
}

.sedona__pic--one {
  margin-top: 2vh; 

  @media (min-width: $desktop-width) {
    width: 550px;
    height: 336px;
    margin-top: 11vh;
  }
}

.sedona__pic--modal {
  margin-top: 2vh; 

  @media (min-width: $desktop-width) {
    width: 550px;
    height: 336px;
    margin-top: 11vh;
  }
}


.sedona__pic--two {
  @media (min-width: $desktop-width) {
    width: 550px;
    height: 202px;  
  } 
}

.sedona__pic--three {
  @media (min-width: $desktop-width) {
    width: 550px;
    height: 401px;  
    transition: 1s;
  } 
}

.sedona__pic--three:hover {
  transform: scale(1.1);
}

.sedona__heading {
  font-size: 2.5em;
  padding-bottom: 0.5em;  
  margin: 0;

  @media (min-width: $desktop-width) {
    font-size: 3.7em;
    line-height: 0.8em;
    min-height: 100px;
  }
}

.sedona__link {
  font-size: 2em;
  line-height: 1.5em;
  text-decoration: none;  
  color: $black;
  cursor: pointer;
  margin-bottom: 0.7em;

  @media (min-width: $desktop-width) {
    font-size: 2.5em;
    line-height: 1em;  
  }
}

.sedona__link:hover {
  color: $pink;
}

.sedona__link:active {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
  color: transparentize($color: $pink, $amount: 0.7);
}

.sedona__link:focus {
  outline-style: none;
  outline-width: 0px;
  outline-color: none;
}

.sedona__content {
  font-size: 1em;
  line-height: 1.4em;
  text-align: justify;
  text-indent: 1em;
  margin: 0;
  width: 80%;
  max-width: 330px;

  @media (min-width: $desktop-width) {
    width: calc(100% - 550px);  
    flex-basis: 28%;  
    order: 0;    
  }
}

.sedona__image:first-child {
  @media(min-width: $desktop-width) {
    order: 1;  
  }
}

.sedona__image:last-child {
  @media(min-width: $desktop-width) {
    order: 2;  
  }
}

.single-item {
  width: 330px;

  @media (min-width: $desktop-width) {
    width: 550px;
  }  
}

.multiple-items {
  width: 330px;

  @media (min-width: $desktop-width) {
    order: 1;
    width: 550px;
  }
}
